import { http } from '../utils/http';
import FileSaver from 'file-saver';
import { useParams } from 'react-router';

export const getAllExport = (roleName) => {
    return new Promise((resolve, reject) => {
        // let { role } = useParams();

        http(
            process.env.REACT_APP_ROOT_URL,
            'GET',
            `users-service/users/users/export/excel?role=${roleName}`,
            {},
            true,

            {
                Accept: '*/*',
                'Accept-Encoding': 'gzip, deflate, br'
            },
            { responseType: 'blob' }
        )
            .then((data) => {
                FileSaver.saveAs(new Blob([data]), 'excel.xlsx');
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};

