import React, { useState, useEffect } from 'react';
import MainCard from '../../ui-component/cards/MainCard';
import { useDispatch, useSelector } from 'react-redux';
import { BOOK_ID } from '../../store/actions';
import { Link, useHistory } from 'react-router-dom';
import AddIcon from '@material-ui/icons/Add';
import { withStyles, useTheme } from '@material-ui/styles';
import {
    Grid,
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip,
    CircularProgress,
    Box,
    Checkbox
} from '@material-ui/core';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import { IconSearch } from '@tabler/icons';
import EditIcon from '@material-ui/icons/Edit';
import SubjectIcon from '@material-ui/icons/Subject';
import ConfirmDialog from '../../ui-component/ConfirmDialog/ConfirmDialog';
import DeleteMsg from '../../ui-component/messages/DeleteMsg';
import useStyles from './styles';
import { deleteBooks, getBooks } from '../../store/actions/BookActions';
import useUserRole from '../../hooks/useUserRole';
import { setMessage } from '../../store/actions/MessageActions';
import { bookUpload } from '../../services/ProfileUpload';
import { getBookExport } from '../../services/export';
import { Alert, Snackbar, useMediaQuery } from '@mui/material';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function Book() {
    useUserRole({ roleList: ['ADMIN', 'SUPER_ADMIN'] });
    const classes = useStyles();
    const history = useHistory();
    const dispatch = useDispatch();
    const theme = useTheme();
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const [isDeleted, setIsDeleted] = useState(false);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const { allBooks } = useSelector((state) => state.books);
    const [isSearchBook, setSearchBook] = useState('');
    const [file, setFile] = useState(null);
    const [selected, setSelected] = useState([]);
    const [loading, setLoading] = useState(false);
    const [dowmloadFail, setDownloadFail] = useState(false);

    const isSelected = (name) => selected.indexOf(name) !== -1;
    const [uploading, setUploading] = useState(false);

    const handleBookId = (id) => {
        dispatch({ type: BOOK_ID, payload: { book_id: id } });
        history.push(`/admin/books/update-book/${id}`);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };
    const handleSearchBook = (event) => {
        setSearchBook(event.target.value);
        setPage(0);
    };

    const [confirmDialog, setConfirmDialog] = useState({
        isOpen: false,
        title: '',
        subTitle: ''
    });
    const CSVUpload = async () => {
        try {
            setUploading(true);
            const formData = new FormData();
            formData.append('files', file);
            await bookUpload(formData, dispatch, setMessage);
            dispatch(getBooks());
        } catch (error) {
            console.log(error);
        } finally {
            setUploading(false);
        }
    };

    const handleFileChange = async (event) => {
        const selectedFile = event?.target?.files[0];
        setFile(selectedFile);

        if (selectedFile) {
            try {
                setUploading(true);
                const formData = new FormData();
                formData.append('files', selectedFile);
                await bookUpload(formData, dispatch, setMessage);
                // showNotification('CSV uploaded successfully!');
                dispatch(getBooks());
            } catch (error) {
                console.log(error);
                setMessage(error?.data?.message);
            } finally {
                setUploading(false);
            }
        }
    };

    const handleSelectAllClick = (event) => {
        if (event.target.checked) {
            const newSelecteds = allBooks?.content?.map((n) => n.id);
            setSelected(newSelecteds);
            return;
        }
        setSelected([]);
    };
    const exportBook = async () => {
        setDownloadFail(false);
        setLoading(true);

        await getBookExport(selected)
            .then((r) => {
                setLoading(false);
            })
            .catch((error) => {
                setLoading(false);
                setDownloadFail(true);
                console.log(error);
            });
    };

    const handleDownload = () => {
        const link = document.createElement('a');
        link.href = 'https://mum-objectstore.e2enetworks.net/bookeinstein/SampleBook.csv';
        link.download = 'Sample Book.csv';
        link.target = '_blank';

        link.dispatchEvent(new MouseEvent('click'));

        document.body.removeChild(link);
    };

    const handleClick = (event, name) => {
        const selectedIndex = selected.indexOf(name);

        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
        }

        setSelected(newSelected);
    };

    const handleDeleteBook = (id) => {
        const payload = {
            status: 'INACTIVE'
        };
        dispatch(
            deleteBooks(payload, id, () => {
                dispatch(getBooks(isSearchBook ? isSearchBook : '', page + 1, rowsPerPage));
            })
        );
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        });
    };

    useEffect(() => {
        dispatch(getBooks(isSearchBook ? isSearchBook : '', page + 1, rowsPerPage));
    }, [rowsPerPage, isSearchBook, page]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; All Books </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6} md={3}>
                            <OutlinedInput
                                className={classes.searchControl}
                                id="input-search-header"
                                value={isSearchBook}
                                onChange={(event) => handleSearchBook(event)}
                                placeholder="Search"
                                startAdornment={
                                    <InputAdornment position="start">
                                        <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                    </InputAdornment>
                                }
                            />
                        </Grid>

                        <Grid item xs={12} sm={12} md={4}>
                            <div>
                                <input
                                    type="file"
                                    accept=".csv"
                                    onChange={(e) => handleFileChange(e)}
                                    style={{ display: 'none' }}
                                    id="file-input"
                                />
                                <label htmlFor="file-input">
                                    <Button
                                        variant="contained"
                                        component="span"
                                        style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                                        startIcon={<CloudUploadIcon />}
                                        title="⚠️ Keep ISBN Column in Number Format. Right-click -> Format Cell -> Number -> Decimal places: 0 -> Select: 1234"
                                    >
                                        Upload CSV
                                    </Button>
                                </label>
                                {uploading && <CircularProgress />}
                            </div>
                        </Grid>
                        <Grid item>
                            <div className={classes.btn}>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    className={classes.btnAddPlanOptions}
                                    component={Link}
                                    to="/admin/books/add-book"
                                    style={{ cursor: 'pointer' }}
                                >
                                    <AddIcon /> Book
                                </Button>
                            </div>
                        </Grid>
                        <Grid item>
                            <Button
                                variant="contained"
                                color="primary"
                                style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                                onClick={exportBook}
                            >
                                Export
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button
                                variant="contained"
                                color="primary"
                                style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                                onClick={handleDownload}
                            >
                                Download Format
                            </Button>
                        </Grid>
                        <Grid sm={2} xs={12} item display="flex" justifyContent="center">
                            {loading ? (
                                <>
                                    <h7>Downloading...</h7>{' '}
                                    <Box sx={{ display: 'flex' }}>
                                        <CircularProgress />
                                    </Box>
                                </>
                            ) : (
                                <></>
                            )}
                            {dowmloadFail ? <Alert severity="error">Downloading Failed !</Alert> : <> </>}
                        </Grid>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell padding="checkbox">
                                        <Checkbox
                                            indeterminate={selected?.length > 0 && selected?.length < allBooks?.content?.length}
                                            checked={allBooks?.content?.length > 0 && selected?.length === allBooks?.content?.length}
                                            onChange={handleSelectAllClick}
                                            inputProps={{ 'aria-label': 'select all items' }}
                                        />
                                    </TableCell>
                                    <TableCell> Cover </TableCell>
                                    <TableCell> Title </TableCell>
                                    <TableCell> Author </TableCell>
                                    <TableCell> Publisher </TableCell>
                                    <TableCell> Isbn No. </TableCell>
                                    <TableCell align="center"> Shelf No. </TableCell>
                                    <TableCell align="center"> Status </TableCell>
                                    <TableCell align="right"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            <TableBody>
                                {allBooks?.content?.map((row, index) => {
                                    const isItemSelected = isSelected(row?.id);

                                    const labelId = `enhanced-table-checkbox-${index}`;
                                    return (
                                        <StyledTableRow key={row?.id}>
                                            <TableCell padding="checkbox">
                                                <Checkbox
                                                    checked={isItemSelected}
                                                    inputProps={{ 'aria-labelledby': labelId }}
                                                    onChange={(event) => handleClick(event, row?.id)}
                                                />
                                            </TableCell>
                                            <TableCell>
                                                <img src={row?.imageUrls?.[0]} alt="book" width="60px" height={65} />
                                            </TableCell>
                                            <TableCell> {row?.title} </TableCell>
                                            <TableCell> {row?.author} </TableCell>
                                            <TableCell> {row?.publisher} </TableCell>
                                            <TableCell> {row?.isbn} </TableCell>
                                            <TableCell align="center"> {row?.shelfId} </TableCell>
                                            <TableCell align="center">
                                                <Typography
                                                    className={classes.status}
                                                    style={{
                                                        backgroundColor:
                                                            (row?.status === 'ACTIVE' && theme.palette.success.main) ||
                                                            (row?.status === 'PENDING' && 'blue') ||
                                                            (row?.status === 'INACTIVE' && theme.palette.warning.dark)
                                                    }}
                                                >
                                                    {row?.status}
                                                </Typography>
                                            </TableCell>
                                            <TableCell align="right">
                                                <Tooltip title="Update Book">
                                                    <IconButton>
                                                        <EditIcon
                                                            color="primary"
                                                            style={{ cursor: 'pointer' }}
                                                            onClick={() => handleBookId(row?.id)}
                                                        />
                                                    </IconButton>
                                                </Tooltip>
                                                {loggedUserProfile?.role === 'SUPER_ADMIN' && (
                                                    <Tooltip title="Delete Book">
                                                        <IconButton
                                                            onClick={() => {
                                                                setConfirmDialog({
                                                                    isOpen: true,
                                                                    title: 'Delete Book',
                                                                    subTitle: 'Are you sure you want to delete ?',
                                                                    onConfirm: () => {
                                                                        handleDeleteBook(row.id);
                                                                    }
                                                                });
                                                            }}
                                                        >
                                                            <DeleteForeverIcon color="error" style={{ cursor: 'pointer' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                )}
                                            </TableCell>
                                        </StyledTableRow>
                                    );
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={allBooks?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>

                {confirmDialog && <ConfirmDialog confirmDialog={confirmDialog} setConfirmDialog={setConfirmDialog} />}

                <DeleteMsg isDeleted={isDeleted} setIsDeleted={setIsDeleted} message={'Book Deleted Successfully'} />
            </MainCard>
        </>
    );
}
